@import "mixin";

@media all and (max-width: 991px) {
	.header {
		padding-bottom: 0;
		.header-form {
			margin-bottom: 0;
			border-radius: 0;
			.btn {
				border-radius: 0; } } }

	// .social
	// 	.social-h
 }	// 		margin-top: 10px

// @media all and (max-width: 668px)
@media all and (max-width: 768px) {

	.feature-1 {
		.tabs {
			padding: 0;
			.tab {
				margin-left: 0;
				margin-right: 0;
				border-radius: 0; } }
		.btn {
	    position: relative;
  	  left: 50%;
	    transform: translateX(-50%); }

		.features-h {
			margin-top: 20px !important; } }

	.section-h {
		font-size: 28px; }

	.process {
		.process-icon {
			font-size: 20px;
			padding: 14px 22px; }
		.process-column:nth-child(2) {
			.process-icon {
				padding: 14px 17px; } }
		.process-content {
			margin-top: 30px; } }


	.pricing {
		.pricing-content {
			margin-top: 0px;
			.pricing-slot:hover {
				.package {
					@include transition-ease(0.3s); } }

			.pricing-slot:hover {
				.recommendation {
					border: 2px solid $greytext; } } } }

	.testimonials>.site-text {
			margin-bottom: 0; }

	section {
		.site-text {
 } } }			// margin-bottom: 20px !importaprocessnt

@media all and (max-width: 766px) {

	.feature-2,
	.feature-3 {
		.feature-img {
			display: none; } }

	.process {
		.process-column {
			height: 116px;
			overflow: hidden;
			@include transition-ease(0.3s); }

		.site-text {
			@include translate(0, -50px);
			opacity: 0;
			@include transition-ease(0.3s); }

		.site-t {
			opacity: 1;
			@include translate(0, 0px); } }

	.process-column:hover {
		height: 221px; }

	.process-column:hover .site-text {
		height: 221px;
		// display: block
		opacity: 1;
		@include translate(0, 0px); }


	.pricing {
		.pricing-content {
			.pricing-slot {
				height: 69px;
				overflow: hidden;
				@include transition-ease(0.5s);
				.package {
					@include translate(0, 0px); }
				.recommendation {
					box-sizing: border-box;
					border: 2px solid $greytext; }
				.package-list {
					// @include translate(0, -100%)
					position: relative;
 } }					// top: -150px
			.pricing-slot:hover {
				height: 550px; } } } }

@media all and (max-width: 425px) {

	.social {
		.social-icon {
			font-size: 30px; } }

	.section-h {
		font-size: 24px; }



	.pricing {
		.pricing-content {
			.pricing-slot {
				height: 69px;
				overflow: hidden;
				@include transition-ease(0.5s);
				.package {
					@include translate(0, 0px); }
				.recommendation {
					box-sizing: border-box;
					border: 2px solid $greytext; }
				.package-list {
					// @include translate(0, -100%)
					position: relative;
 } }					// top: -150px
			.pricing-slot:hover {
				height: 550px; } } } }
